/* @import '~antd/dist/antd.css'; */

body {
  margin: 0;
  /* font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif; */
    font-family: Arial, Helvetica, sans-serif !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #F5F6F7 !important;
  width: auto;
  height: auto;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* for search input */
.ant-input-affix-wrapper:not(.ant-input-affix-wrapper-disabled):hover {
  border-radius: 5px;
  border-color: #105177;
}

.ant-input-affix-wrapper:not(.ant-input-affix-wrapper-disabled) {
  border-radius: 5px;
  /* border-color: #105177; */
}

/* for create button */
#createButton{
  border-radius: 5px;
  padding: 0 13px;
  color: black;
  background: #66C833 0% 0% no-repeat padding-box;
  font-weight: bold;
  border: none;
  }

  /* for making sorting icon next to the heading text of the table */
 .teamBossTableLayout .ant-table-column-sorters {
    width: fit-content;
}
.teamBossTableLayout .ant-table-column-sorter {
    margin-left: 10px;
}

/* for table layout */
.teamBossTableLayout .ant-table .ant-table-body table{
  background-color: transparent;
  border-collapse: separate;
border-spacing: 0 5px;
}

.teamBossTableLayout .ant-table{
  background: transparent;
  }
  
  .teamBossTableLayout .ant-table table tr {
      background-color: #fff;
  }
  
  .teamBossTableLayout .ant-table table .ant-table-thead tr th{
      background-color: #f0f2f5;
      font-weight: bold;
  }

  .ant-table.ant-table-small .ant-table-title, .ant-table.ant-table-small .ant-table-footer, .ant-table.ant-table-small .ant-table-thead > tr > th, .ant-table.ant-table-small .ant-table-tbody > tr > td, .ant-table.ant-table-small tfoot > tr > th, .ant-table.ant-table-small tfoot > tr > td {
      padding: 8px 8px;
      background-color: transparent;
  }

  .ant-form-item-explain-error {
    text-align: left !important;
}

.ant-form-item-explain-error::first-letter {
  text-transform: uppercase;
}

/* for showing asterisk(*) symbol right of the label  */

.ant-form-item-label > label.ant-form-item-required:not(.ant-form-item-required-mark-optional)::after {
  display: inline-block;
  margin-left: 4px;
  color: #ff4d4f;
  font-weight:normal;
  font-size: 14px;
  font-family: SimSun, sans-serif;
  line-height: 1;
  content: '*';
}

.ant-form-item-label > label.ant-form-item-required:not(.ant-form-item-required-mark-optional)::before {
content: none !important
}
