.appBg{
  display:flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100vw;

}

.loginForm{
background-color: rgba(255,255,255,0.33);
box-shadow:   0 3px 10px rgb(0 0 0 / 0.2);
padding: 3%;
width:50%;
padding:0px 16px 16px 16px;
border-radius: 15px;
}

.formTitle{
  text-align: center;
}

.formActions{
  margin-bottom: 10px;
}

@media (max-width: 1200px) {
  .login-form {
      align-items: center;
      justify-content: center;
      padding: 2%;
      background: #FFFFFF 0% 0% no-repeat padding-box;
      box-shadow: 0px 5px 25px #0000001A;
      border-radius: 5px;
      margin-top: 10%;
      margin-bottom: 35%;
      margin-left: 20%;
      margin-right: 20%;
  }
}

.login-form-button.ant-btn {
  width: 100%;
  background-color: #6ba958;
  color: #FFFFFF;
  margin-top: 20px;
}


.login-form-button.ant-btn:hover {
  background-color: #6ba958;
  color: #FFFFFF !important;
}







