.carePlanBg{
  
    justify-content: center;
    align-items: center;
    height: 150vh;
    width: 80vw;
  
  }
  
  .carePlanMain{
  background-color: rgba(255,255,255,0.33);
  box-shadow:   0 3px 20px rgb(0 0 0 / 0.2);
  padding: 2%;
  padding:0px 16px 16px 16px;
  border-radius: 15px;
  
  }
  
  .formTitle{
    text-align: center;
  }
  
  .formActions{
    margin-bottom: 10px;
  }
  
  @media (max-width: 1200px) {
    .login-form {
        align-items: center;
        justify-content: center;
        padding: 2%;
        background: #FFFFFF 0% 0% no-repeat padding-box;
        box-shadow: 0px 5px 25px #0000001A;
        border-radius: 5px;
        margin-top: 10%;
        margin-bottom: 35%;
        margin-left: 20%;
        margin-right: 20%;
    }
  }
  
  .login-form-button.ant-btn {
    width: 100%;
    background-color: #6ba958;
    color: #FFFFFF;
    margin-top: 20px;
  }
  
  
  .login-form-button.ant-btn:hover {
    background-color: #6ba958;
    color: #FFFFFF !important;
  }

  .inner-box{
    background-color: #93a5aa00;
    display: flex;
    flex-direction: row;
    padding:5%

  }

  .profileImage {
    width: 70px;
    height: 70px;
    border-radius: 50%;
    background: #037876;
    font-size: 35px;
    color: #fff;
    text-align: center;
   
    
   
  }

 

  .divider {
    display: inline-flex;
    width: 2px;
    background-color: black;
    margin: 0 10px;
    height: 5em;
  }

  #inputID::placeholder{
    color: "black";
    font-weight: 700;
  }

  .subContent-1{
  box-shadow:   0 3px 20px rgb(0 0 0 / 0.2);
  border-color: "black";
  padding: 2%;
  padding:0px 16px 16px 16px;
  border-radius: 15px;
  height: 23vh;
  width: 98%;
  margin-top: 3%;
  margin-left: 1%;
  

  }
  

   
  

  
  
  
  
  
  
  
  